// for showing field in student side

export const studentApplyFormFieldDetailFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/v1/department/dynamic/form/details/query?flow=${data?.flow}&did=${data?.flowId}`,
    transformResponse: (response) =>
      response?.ins_form ? response?.ins_form : response?.depart_form,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

//for student profile detail

export const studentProfileAboutDetailFunction = (builder) => {
  return builder.query({
    query: (data) => `/v1/department/${data.sid}/dynamic/form/query`,
    // keepUnusedDataFor: 0,
    transformResponse: (response) => response?.result,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};
